  合肥: [117.27, 31.86],
  武汉: [114.31, 30.52],
  大庆: [125.03, 46.58]
}
export default {
  name: 'world',
  components: {},
  data() {
    return {
      msg: '',
      zoom: 13,
      mapHeight: 400,
      mapwidth: 400,
      arrive: 9,
      proportion: 0.12
    }
  },
  created: function () {
    let value = this.$route.params.mapHeight * this.proportion
    this.mapHeight = this.$route.params.mapHeight - value
    this.mapwidth = this.$route.params.mapHeight - value
  },
  computed: {
    getTheme: function () {
      let theme = this.$getTheme()
      return theme
    }
  },
  mounted: function () {
    //
    let main = document.getElementById('main')
    var mapChart = this.$echarts.init(document.getElementById('main'))
    let _this = this

    var convertData = function (data) {
      var res = []
      let _arrive = 0
      for (var i = 0; i < data.length; i++) {
        var geoCoord = geoCoordMap[data[i].name]
        if (geoCoord) {
          if (data[i].value > 30) {
            _arrive++
          }
          res.push({
            name: data[i].name,
            value: geoCoord.concat(data[i].value)
          })
        }
      }
      if (_arrive > _this.arrive) {
        _this.arrive = _arrive
      }
      return res
    }
    var option = {
      title: {
        text: '旅行途径',
        subtext: '人生好比攀峰，路是越走越长',
        left: 'center',
        textStyle: {
          color: _this.getTheme.color ? _this.getTheme.color : '#333'
        }
      },
      backgroundColor: _this.getTheme.backgroundColor ? _this.getTheme.backgroundColor : '#fdfdfd',
      tooltip: {
        show: false,
        trigger: 'item'
      },
      geo: {
        map: 'china',
        zoom: 1,
        right: '0%',
        left: '0%',
        label: {
          normal: {
            show: true,
            color: '#fff'
          },
          emphasis: {
            show: false
          }
        },
        roam: false,
        itemStyle: {
          normal: {
            borderWidth: 1,
            areaColor: 'rgba(128, 128, 128, 0)',
            borderColor: '#ffffff'
          },
          emphasis: {
            areaColor: '#3EF3F4'
          }
        }
      },
      series: [
        {
          name: '',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: convertData(data),
          symbolSize: function (val) {
            return val[2] / 10
          },
          label: {
            normal: {
              formatter: '{b}',
              position: 'right',
              show: false
            },
            emphasis: {
              show: false
            }
          },
          itemStyle: {
            normal: {
              color: '#5a5a5a'
            }
          }
        },
        {
          name: '来过',
          type: 'effectScatter',
          coordinateSystem: 'geo',
          data: convertData(
            data
              .sort(function (a, b) {
                return b.value - a.value
              })
              .slice(0, _this.arrive)
          ),
          //标记大小，地图上的圆点
          symbolSize: function (val) {
            return val[2] / 10
          },
          showEffectOn: 'render',
          rippleEffect: {
            brushType: 'stroke'
          },
          hoverAnimation: true,
          label: {
            //地图黄点显示内容
            normal: {
              formatter: '{b}',
              position: 'right',
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#b51808',
              shadowBlur: 10,
              shadowColor: '#333'
            }
          },
          zlevel: 1
        }
      ]
    }
    mapChart.setOption(option)
    mapChart.resize()
  }
}
</script>

<style scoped="scoped">
.home_egg {
  overflow: hidden !important;
}
</style>



